import { SortBy } from '../../types/SortBy';

type FilterTodo = {
  onSelect: (value: SortBy) => void,
  onInput: (value: string) => void,
  query: string,
  onCross: (value: string) => void,
};

export const TodoFilter: React.FC<FilterTodo> = ({
  onSelect,
  onInput,
  query,
  onCross,
}) => {
  return (
    <form className="field has-addons">
      <p className="control">
        <span className="select">
          <select
            onChange={(e) => onSelect(e.target.value as unknown as SortBy)}
            data-cy="statusSelect"
          >
            <option value={SortBy.all}>All</option>
            <option value={SortBy.active}>Active</option>
            <option value={SortBy.completed}>Completed</option>
          </select>
        </span>
      </p>

      <p className="control is-expanded has-icons-left has-icons-right">
        <input
          data-cy="searchInput"
          type="text"
          className="input"
          placeholder="Search..."
          value={query}
          onChange={(e) => onInput(e.target.value)}
        />
        <span className="icon is-left">
          <i className="fas fa-magnifying-glass" />
        </span>

        <span className="icon is-right" style={{ pointerEvents: 'all' }}>
          {/* eslint-disable-next-line jsx-a11y/control-has-associated-label */}
          <button
            data-cy="clearSearchButton"
            type="button"
            className="delete"
            onClick={() => onCross(query)}
          />
        </span>
      </p>
    </form>
  );
};
